  .header-menu__list {
    gap: 24px;
  }
}
.header-menu__item--submenu {
  align-self: flex-start;
  position: relative;
  padding-right: 24px;
  cursor: pointer;
}
.header-menu__item--submenu::before, .header-menu__item--submenu::after {
  content: "";
  position: absolute;
  width: 8px;
  height: 2px;
  background-color: #5b5f63;
  right: 12px;
  top: 13px;
  transition: all 0.3s ease;
}
.header-menu__item--submenu::before {
  transform: translateY(-50%) rotate(45deg);
  right: 10px;
}
.header-menu__item--submenu::after {
  transform: translateY(-50%) rotate(-45deg);
  right: 5px;
}
@media (min-width: 1200px) {
